/*------------------------------------------------------------------*/
/*-- CREATE PROJECT PAGE SPECIFIC --*/
/*------------------------------------------------------------------*/
.create-project-fieldset {
  padding: 0;
  display: none;
}

.fieldset-active {
  display: inherit;
}

.cpp-error {
  display: none;
  font-size: 0.9rem;
  font-weight: 700;
  line-height: 1.8rem;
  background: none;
  border: none;
}

.error-box {
  margin-bottom: 2rem;
  background: tint($orange, 60%) !important;
  border: 1px dashed $orange !important;
}

.form-input-error {
  box-shadow: inset 0 0.5rem 0 0 $orange;

  &:hover {
    box-shadow: inset 0 0.5rem 0 0 $orange;
  }

  &:focus {
    box-shadow: inset 0 0.5rem 0 0 $orange;
  }
}

.form-section-tab-error {
  border-top: 4px solid $orange;
}

.form-group {
  margin-top: 1rem;
  margin-bottom: 0;
}

.form-pair {
  @media all and (max-width: 768px) {
    margin-top: 1rem;
  }
}

.form-button {
  padding: 0.65rem;
  font-weight: 500;
  color: $white;
  background: $green;
  border: 1px solid darken($green, 10%);
  border-radius: 0.35rem;
  cursor: pointer;
  transition: background 0.3s ease;

  &:hover {
    background: lighten($green, 10%);
  }
}

.form-button-secondary {
  position: relative;
  display: inline-block;
  background: $bluegrey;
  border: 1px solid darken($bluegrey, 10%);
  transition: background 0.3s ease;

  &:hover {
    background: lighten($bluegrey, 10%);
  }

  a {
    color: $white;

    &:hover, &:active, &:visited, &:focus {
      color: $white;
      text-decoration: none !important;
    }
  }
}

.form-button-nav {
  display: inline-block;
  background: $blue;
  border: 1px solid darken($blue, 10%);
  transition: background 0.3s ease;

  &:hover {
    background: lighten($blue, 10%);
  }
}

.form-button-full-width {
  width: 100%;
}

.image-upload-wrapper {
  position: relative !important;

  @media all and (max-width: 768px) {
    margin-bottom: 1.25rem;
  }
}

.image-loader {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: url("../img/loader.gif") no-repeat center center;
  display: none;
}

.image-upload-preview {
  width: 100%;
  display: none;
}

.image-upload-label {
  z-index: 1001;
  width: 100%;
  padding: 1.25rem;
  margin-bottom: 0;
  background: tint($blue, 75%);
  border: 1px dashed $blue;
  cursor: pointer;
}

.image-upload-label-heading {
  margin-top: 10px;
  color: $blue;
}

.image-upload-input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  visibility: hidden;
}

.image-upload-close-button {
  position: absolute;
  top: -2%;
  display: inline-block;
  width: 1.5rem;
  height: 1.5rem;
  padding-top: 2px;
  border-radius: 50%;
  font-family: $montserrat;
  text-align: center;
  font-weight: 500;
  color: $white;
  background: $bluegrey;
  cursor: pointer;
  transition: background 0.3s ease;

  &:hover {
    background: lighten($bluegrey, 10%);
  }
}

.label-desc {
  font-size: 1rem;
  font-weight: normal;
}

.form-explanation {
  padding: 1.25rem;
  margin-bottom: 1rem;
  border: 1px solid $dgrey;
  color: $blue;
  
  p:first-of-type {
    margin-top: 10px;
  }
}

.form-input-disabled {
  cursor: not-allowed;
}

.no-projects {
  padding: 1.25rem;
  border: 1px solid $dgrey;
  background: $lgrey;

  p:first-of-type {
    margin-top: 10px;
  }
}

.project-links {
  @extend .no-projects
}

.saved-project-title {
  font-size: 1.1em;
  font-weight: 700;
  color: $bluegrey;
}

.saved-project-continue {
  @extend .form-button;
  display: inline-block;
  width: 100%;

  &:visited, &:focus, &:active, &:hover {
    text-decoration: none;
    color: $white;
  }
}

.saved-project-delete {
  @extend .saved-project-continue;
  background: $bluegrey;
  border: 1px solid darken($bluegrey, 10%);
  border-radius: 0.35rem;

  &:hover {
    background: lighten($bluegrey, 10%);
  }
}

.supporting-doc {
  margin-bottom: 2rem;

  @media all and (max-width: 768px) {
    margin-bottom: 20px;
  }
}

.bt-none {
  border-top: none;
}

.padding-none {
  padding: 0;
}

.display {
  display: block;
}

.display-none {
  display: none;
}

.text-right {
  text-align: right;
}

.character-count {
  display: none;
}

.summary-page-button {
  margin-top: 0.5rem;
}

fieldset.summary-group {
  display: block !important;
  margin-top: 1rem;
  padding: 1rem;
  border: 1px solid $dgrey;

  legend {
    display: inline-block;
    width: auto;
    margin-bottom: 0;
    padding: 0 0.25rem;
    font-size: 0.9rem;
    font-weight: 700;
    color: $bluegrey;
    border: none;
  }

  p {
    margin-bottom: 0;
  }
}

.success-container {
  @extend .form-element;

  margin-top: 2rem;
  margin-bottom: 1rem;
  padding: 0 1rem 1rem 1rem;
  border-radius: 0;
}

.success-heading {
  margin: 0 -15px 1rem -15px !important;
  padding: 0.5rem 0;
  font-size: 1.5rem;
  font-weight: 700;
  text-shadow: 1px 1px 1px darken($green, 15%);
  color: $white;
  background: $green;
  box-shadow: 1px 2px 2px rgba(0,0,0,0.3);
}

.success-sub-heading {
  margin-top: 1rem;
  margin-bottom: 0;
  padding-bottom: 1rem;
  font-size: 1.25rem;
  font-weight: 500;
  color: $bluegrey;
}

.success-page-link {
  transition: color 0.3s ease;
}
